import React, { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import OffCanvas from "../OffCanvas";
import BrandLogo2 from "../../assets/images/BrandLogo2.png";
import { PersonCircle, CartFill } from "react-bootstrap-icons";
import Banner from "../Banner";
import "./index.css";
import Typer from "../Typer";
import { useSelector } from "react-redux";

const Header = () => {
  const navigate = useNavigate();
  const selectedList = useSelector(state => state.cart.itemsList)
  const isUserLogin = useSelector(state => state.user.currentUser.userId ? true : false)
  const [searchVal, setSearchVal] = useState(sessionStorage.getItem('emsebach') ?? '');
  useEffect(() => {
    window.addEventListener('scroll', isSticky);
    return () => {
      window.removeEventListener('scroll', isSticky);
    };
  });

  const isSticky = (e) => {
    const header = document.querySelector('.App-header');
    const scrollTop = window.scrollY;
    // scrollTop >= 100 ? header.classList.add('is-sticky') : header.classList.remove('is-sticky');
  };

  // useEffect(()=>{
  //  setSearchVal(sessionStorage.getItem('emsebach'))
  //  console.log('selc')
  // },[sessionStorage.getItem('emsebach')])

  return (
    <>
      <header className="App-header">
        <Banner data="FREE SHIPPING ABOVE &#8377;500" />
        <div className="headerContainer d-flex justify-content-between align-items-center">
          <div className="col-lg-4 col-md-2 col-sm-2">
            <OffCanvas />
          </div>
          <div className="col-lg-4 col-md-3 col-sm-8 text-center text-md-end text-lg-center">
            <Link to="/"><img src={BrandLogo2} className="brandlogo" alt="logo" height={60} width={210} /></Link>
          </div>
          <div className="col-lg-4 col-sm-2 d-flex text-center">
            <span className="me-3">
              <Typer
                dataElementId="searchBar"
                dataText={[
                  'Search Perfumes',
                  'Bath and Body',
                  "Search Gift Sets"
                ]}
                navigate={navigate}
                searchValue={searchVal}
              />
            </span>
            <span className="me-3">
              <Link to={`${isUserLogin?"/my-account/edit-account":"/sign-in"}`}><PersonCircle style={{ color: "#000", height: "32px", width: "32px" }} /></Link>
            </span>
            <Link to="/cart" style={{ color: "#000" }}>
              <span style={{ position: "relative" }}>
                {!selectedList.length == 0 && (
                  <span className="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-danger">
                    {selectedList.length}
                    <span className="visually-hidden">unread messages</span>
                  </span>
                )}
                <CartFill style={{ height: "32px", width: "32px" }} />
              </span>
            </Link>
          </div>
        </div>
      </header>
    </>
  );
};

export default Header;